#card {
  margin: 0px;
  padding: 0.5rem;
  color: #abbec4;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  object-fit: contain;
  justify-content: center;
  align-items: center;
  /* max-height: 100px; */
}

#cardimg{
  flex:1;
  max-width: 80%;
  max-height: 50%;
  /* height: auto; */
  /* object-fit: contain; */
}

.socials {
  display: flex;
  width: 100%;
  justify-content: center;
  gap: 0.5rem;

}

.socials a img {
  width:1rem;
  height:1rem;
  object-fit: cover;
}

#genreTags{
  color:rgba(10, 10, 10, 0.815);
}